:root{
    --poppin : 'Poppins', sans-serif;
    --basefont : 'Roboto', sans-serif;
    --textfont : 'Rubik', sans-serif;
}
*{
    font-family: 'Roboto', sans-serif;
    font-family: var(--basefont);
}
body{
    margin: 0;
    padding: 0;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow-x: hidden;
}

