.login_signup_box{
    display: flex;
    flex-direction: column;
    min-height: 60vh;
    max-height: 80vh;
    width: 40vw;
    background-color: white;
    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
    text-align: center;
    position: relative;
}
.login_signup_box_head{
    width: 100%;
    height: 50px;
    background-color: #0079d6;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: white;
    position: relative;
    top: 0;
}

.login_signup_box_head_img{
    position: absolute;
    left: 10px;
    width: 50px;
    height: 50px;
}
.login_signup_box_head_text{
    color: white;
    font-family: 'Poppins', sans-serif;
    font-family: var(--poppin);
    font-weight: 600;
}

.login_signup_box_foot{
    bottom: 0;
    width: 100%;
    min-height: 44px;
    padding: 3px 0;
    max-height: -webkit-fit-content;
    max-height: -moz-fit-content;
    max-height: fit-content;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    background-color: #0079d6;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.login_signup_box_foot_text{
    font-size: 18px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0;
    padding: 0;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-family: 'Poppins', sans-serif;
    font-family: var(--poppin);
    font-weight: 600;
    color: white;
}
.login_signup_box__foot_register{
    margin: 0;
    padding: 0;
    margin-left: 10px;
    font-size: 15px;
    color: rgb(0, 0, 0);
    cursor: pointer;
    transition: all linear 200ms;
    padding: 1px 0;
}
.login_signup_box__foot_register:hover{
    color: rgb(36, 36, 36);
}
/* for code form */
#code{
    display: none;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 50vh;
    max-height: -webkit-fit-content;
    max-height: -moz-fit-content;
    max-height: fit-content;
    padding: 10px 0;
    position: relative;
    background-color: white;
}
#code h3{
    font-family: 'Rubik', sans-serif;
    font-family: var(--textfont);
}
#code input{
    border: none;
    outline: none;
    height: 30px;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px;
    width: 40%;
    font-size: 20px;
    background-color: rgb(218, 218, 218);
    color: black;
    margin-top: 4px;
    margin: 10px 0;
    font-size: 16px;
    font-family: 'Poppins', sans-serif;
    font-family: var(--poppin);
    border-radius: 3px;
}
#code button{
    width: 40%;
    height: 30px;
    font-size: 18px;
    color: white;
    background-color: #0085eb;
    border: none;
    outline: none;
    cursor: pointer;
    transition: all 100ms ease;
    margin-top: 5px;
    font-family: 'Poppins', sans-serif;
    font-family: var(--poppin);
    border-radius: 3px;
}

/* alert box */

.alert {
    opacity: 1;
    position: fixed;
    top: 5px;
    right: 5px;
    padding: 20px;
    background-color: #90ee90;
    color: rgb(248, 248, 248);
    transition: opacity 0.6s;
}

.closebtn {
    margin-left: 15px;
    color: white;
    font-weight: bold;
    float: right;
    font-size: 22px;
    line-height: 20px;
    cursor: pointer;
    transition: 0.3s;
}

.closebtn:hover {
    color: black;
}



.login_signup_box_select{
    width: 100%;
    min-height: 50vh;
    max-height: -webkit-fit-content;
    max-height: -moz-fit-content;
    max-height: fit-content;
    padding: 10px 0;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #ffffff;
}
.username{
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin-bottom: 10px;
    position: relative;
}
.username h4{
    margin: 0;
    padding: 0;
    font-family: 'Roboto', sans-serif;
    font-family: var(--basefont);
    font-size: 16px;
    color: rgba(43, 43, 43, 0.856);
    font-weight: 400;
}
.password{
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin-top: 10px;
}
