}
.password h4 input{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: 16px;
    margin-left: 4px;
    width: 16px;
}
.password input{
    border: none;
    outline: none;
    height: 30px;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px;
    width: 40%;
    font-size: 16px;
    background-color: rgb(218, 218, 218);
    color: black;
    margin-top: 4px;
    font-family: 'Poppins', sans-serif;
