}

@media only screen and (max-width: 1200px) {
    .login_signup_box{
        width: 55vw;
    }
    .username input{
        width: 50%;
    }
    .password input{
        width: 50%;
    }
    .button button{
        width: 50%;
    }
}
@media only screen and (max-width: 600px){
    .login_signup_box{
        width: 70vw;
    }
    .username input{
        width: 70%;
    }
    .password input{
        width: 70%;
    }
    .button button{
        width: 70%;
    }
}

.loader_body{
    width: 100vw;
    display: flex;
    height: 100vh;
    min-height: 100vh;
    z-index: 10000;
    position: fixed;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.90);
}
.loader_body img{
  width: 150px;
  height: 150px;
}
::-webkit-scrollbar {
    width: 10px;
  }
   
::-webkit-scrollbar-thumb {
    background: rgb(162, 168, 255);
    border-radius: 20px;
  }
  
::-webkit-scrollbar-track {
    background: #ddd;
    border-radius: 20px;
  }
  

#navbar{
    width: 100vw;
    min-height: 60px;
    max-height: -webkit-fit-content;
    max-height: -moz-fit-content;
    max-height: fit-content;
    background-color: black;
}
#right_nav{
    position: absolute;
    right: 10px;
}
#left_nav{
    position: absolute;
    left: 10px;
}

.navbar-profile-box{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 0 10px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: center;
    cursor: pointer;
    height: 50px;
    transition: all 200ms ease;
}
.navbar-profile-box:hover{
    background-color: rgba(0, 0, 0, 0.11);
}
.navbar-profile-box img{
    width: 40px;
    border-radius: 50%;
    height: 40px;
    background-position: center;
    vertical-align: middle;
}
.navbar-profile-box h4{
    font-size: 20px;
    color: white;
    margin: 0;
    margin-left: 8px;
    padding: 0;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-family: 'Josefin Sans', sans-serif;
}

